import React from 'react';
import Media from 'react-media';
import style from './information.module.css';
import Buttons from '../../buttons/buttons-dark';
import img from '../../../img/infoImg/office.png';
import imgW from '../../../img/infoImg/office.webp';

const Information = ({ onOpenModal }) => (
  <div className={style.wrapper}>
    <p className={style.text}>
      Современный функциональный Личный кабинет абонента – это мощное средство
      сокращения расходов и увеличения платежей от населения, сбор показаний и
      online-оплаты
    </p>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={imgW} type="image/webp" />
            <source srcSet={img} type="image/png" />
            <img className={style.img} src={img} alt="Office" />
          </picture>
        )
      }
    </Media>
    <Buttons
      path="officeCitizenry"
      onOpenModal={onOpenModal}
      className={style.buttons}
    />
  </div>
);

export default Information;
